    <ld-poplist :showpop=pop_list :data=dataList @popVal=popList @change="changeRule">
    </ld-poplist>
    <div class="infoSection">
      <div class="infoItem">
        <span>
          收款省
        </span>
        <div class="el-input el-input--suffix">
          <el-select v-model="provinceId" placeholder="请选择" @change="selectCitys">
            <!-- <el-option v-for="item in pros" :key="item.id" :label="item.name" :value="item.id">
            </el-option> -->
          </el-select>
        </div>
      </div>
      <div class="infoItem">
        <span>
          收款市
        </span>
        <div class="el-input el-input--suffix">
          <el-select v-model="cityId" placeholder="请选择">
            <!-- <el-option v-for="item in citys" :key="item.id" :label="item.name" :value="item.id">
            </el-option> -->
          </el-select>
        </div>
      </div>
        <div class="infoItem">
