            </el-option> -->
          </el-select>
        </div>
      </div>
    </div>
    <!-- <ld-input label="省市区" v-model="provincecity" placeholder="点击此处选择" @ldFocus="showpoplist('obj')" icon="arrow-right"></ld-input> -->
    <!-- <ld-poplist :showpop=pop_list :data=dataList @popVal=popList @change="changeRule">
        </ld-poplist> -->
    <ld-input label="街道门牌" v-model="shopAddress" @ldFocus="showpoplist('map')" placeholder="请输入地址" icon="location-outline" @clickIcon="changeMap"></ld-input>
