        ["2015-01", "2015-02", "2015-03", "2015-04", "2015-05", "2015-06"]
      ],
      show_pop: false,
      pop_list: false,
      popval: "主要按钮",
      marker: {}
    };
  },
  validators: {//属于自定义属性
    logo: {
      rule: ["required"],
      tip: ["logo为必填项"]
    },
    shopName: {
      rule: ["required"],
