    },
    contactTel: {
      rule: ["required"],
      tip: ["电话为必填项"]
    },
    type: {
      rule: ["required"],
      tip: ["类型为必填项"]
    },
    provinceId: {
      rule: ["required"],
      tip: ["收款省份为必填项"]
    },
    cityId: {
      rule: ["required"],
      tip: ["收款县/市为必填项"]
    },
    areaId: {
