    }
  },
  hello:{
     logo:"hello world"  
  },
  methods: {
    handleAvatarSuccess2(response, file, fileList) {
      var newPic =
        "https://dn-laidian.qbox.me/" +
        response.key +
        "?imageMogr2/thumbnail/750x420!/blur/1x0/quality/75|imageslim";
      this.listshopImg2.push(newPic);
      this.shopImg.push(file.url);
      this.loading = false;
    },
    handleAvatarSuccess1(response, file, fileList) {
      var newPic =
        "https://dn-laidian.qbox.me/" +
        response.key +
        "?imageMogr2/thumbnail/750x420!/blur/1x0/quality/75|imageslim";
      this.listshopImg1.push(newPic);
      this.logo = file.url;
      this.loading = false;
    },
    beforeAvatarUpload2(file) {
      this.formList2.key = hex_md5(Math.random() + new Date());
      console.log(file);
      this.loading = true;
      this.formList2.token =
        "A8SIAnj_MoLaBFRnPlmdCi78eLSUdY57VbMgFJZy:rRiDZbbvugLfAwyJm7DLT-5kGjc=:eyJzY29wZSI6ImNkdC1pbWFnZXMiLCJkZWFkbGluZSI6MTU5NTQ4NTcxMX0="; // uptoken 是上传凭证，由其他程序生成
    },
    beforeAvatarUpload1(file) {
      this.formList1.key = hex_md5(Math.random() + new Date());
      console.log(file);
      this.loading = true;
      this.formList1.token =
        "A8SIAnj_MoLaBFRnPlmdCi78eLSUdY57VbMgFJZy:rRiDZbbvugLfAwyJm7DLT-5kGjc=:eyJzY29wZSI6ImNkdC1pbWFnZXMiLCJkZWFkbGluZSI6MTU5NTQ4NTcxMX0="; // uptoken 是上传凭证，由其他程序生成
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },

    fulladdress(val) {
      console.log(val);
    },
    submit() {
      debugger
       let a= checkAll(this.validators)
        .success(() => {
          this.lhttp(this.$store.state.lhttp.create, {
            remarkUrl: this.remarkUrl,
            logo: this.logo,
            shopName: this.shopName,
            contact: this.contact,
            type: this.type,
            contactTel: this.contactTel,
            shopAddress: this.shopAddress,
            provinceId: this.provinceId,
            cityId: this.cityId,
            areaId: this.areaId,
            shopImg: this.shopImg+"",
            reason: this.reason,
            lat: this.lat,
            lng: this.lng
          }).then(res => {

          });
        })
        .error((item, key) => {
          this.$message.error(item.tip);
        });
    },
    selectCitys() {
      const _this = this;
      this.cityId = "";
      this.lhttp(this.$store.state.lhttp.areaLists, {
        parentId: this.provinceId,
        type: 2
      }).then(data => {
        console.log(data.data.data);
        if (data.data.result == 1) {
          this.citys = data.data.data;
        }
      });
    },
    selectProvince() {
      const _this = this;
      this.lhttp(this.$store.state.lhttp.areaLists, { type: 1 }).then(res => {
        if (res.data.result == 1) {
          this.pros = res.data.data;
          let name = [];
          this.pros.forEach(e => {
            name.push(e.name);
          });
        }
      });
    },
    changeMap() {
      this.$router.push("/choosemap");
    },
    goback() {
      this.returnToSuperior();
    },
    showpoplist(val) {
      if (val == "date") {
        this.pop_list = true;
      } else if (val == "obj") {
        this.pop_list = true;
      } else if (val == "map") {
        // this.$router.push("/choosemap");
      }
    },
