      if (!!val) {
        this.greeting = val;
      }
      this.pop_list = false;
    },
    changeRule({ obj, values }) {
      // debugger;
      // if (values[0] > values[1]) {
      //   obj.setSlotValue(1, values[0]);
      // }
      //  obj.setSlotValue(1, this.dataList[values[0]]);
    }
