function isArrayLike(collection) {
    var length = collection.length
    debugger
    return typeof length === 'number' && length >= 0
}

function each(obj, fn) {
    if (!obj || !fn) return
             debugger
    if (isArrayLike(obj)) {
        for (let i = 0, len = obj.length; i < len; i++) {
            if (fn.call(obj, obj[i], i) === false) return
        }
    } else {
        let keys = Object.keys(obj)
        for (let i = 0, len = keys.length; i < len; i++) {
            let k = keys[i]
            if (fn.call(obj, obj[k], k) === false) return
        }
    }
    
}

function checkAll(validators) {
    debugger
    let flag = true
    var result;
    var prop;
    each(validators, (item, key) => {
        debugger;
        if (!item.isOk) {
            flag = false
            result=item
            prop=key
            return false
        }
    })
    // 返回第一个false
    return {
        success(fn) {
            flag && fn && fn();//只有flag和fn都true 才会调用fn();
            return this;
        },
        error(fn) {
            !flag && fn && fn(result, prop, validators);
            return this;
        }
    }
}

function getType(obj) {
    return Object.prototype.toString.call(obj).slice(8, -1).toLowerCase()
}

function isNull(arg) {
    return arg === null
}

function isUndefined(arg) {
    return arg === undefined
}

// 将普通字符串转义成正则的格式
function escapeStringRegexp(str) {
    let matchOperatorsRe = /[|\\{}()[\]^$+*?.]/g

    if (typeof str !== 'string') {
        throw new TypeError('Expected a string')
    }

    return str.replace(matchOperatorsRe, '\\$&');
}

function injectProp(dataProp, injectObj, vm) {//把验证装进data属性中
    let data = vm.$options.data
    // inject prop in data
    // data为官方定义的data对象属性
    if (typeof data === 'function') {
            let obj = data.call(vm);
        vm.$options.data = function () {
            obj[dataProp] = injectObj
        
            return obj
        }();
        console.log(obj);
        debugger
    } else {
        data[dataProp] = injectObj
    }
    debugger
}


export {
    each, isArrayLike, getType,
    isUndefined, isNull, escapeStringRegexp,
    injectProp, checkAll
}
